.mainmenu{
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 500px;
	height: 585px;
	margin: 0 auto;
	margin-top: 50px;
	background-color: #ccc; 
	-webkit-box-shadow: 3px 3px 0 rgba( 33, 33, 33, 0.65); 
	        box-shadow: 3px 3px 0 rgba( 33, 33, 33, 0.65);
	border-radius: 4px;
}

ul{
	list-style: none;
	margin: 0 auto;
	width: 400px;
	text-align: center;
    padding-top: 40px;
}

li{
   text-decoration: none;
   margin: 0 auto;
   margin-top: 70px;
   border: 2px solid #999;
   width: 300px;

}



